// src/SensorChart.js
import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import { format, parse, subDays, subWeeks, subMonths, subYears, isAfter } from 'date-fns';

const SensorChart = ({ title, data, dataKey, color, timeRange }) => {
  const now = new Date();

  // Define Y-axis labels for different sensors
  const yAxisLabels = {
    temperature: 'Temperature (C)',
    conductivity: 'Conductivity (S/cm)',
    salinity: 'Salinity (ppt)',
    dissolved_solids: 'Total Dissolved Solids (mg/L)',
    dissolved_oxygen: 'Dissolved Oxygen (mg/L)',
  };

  const getStartDate = () => {
    switch (timeRange) {
      case 'Day': return subDays(now, 1);
      case 'Week': return subWeeks(now, 1);
      case 'Month': return subMonths(now, 1);
      case 'Year': return subYears(now, 1);
      default: return subDays(now, 1);
    }
  };

  const startDate = getStartDate();

  // Prepare and filter data based on the time range
  const chartData = Object.keys(data)
    .map((timestamp) => {
      const parsedDate = parse(timestamp, 'yyyy-MM-dd_HH-mm-ss', new Date());
      return {
        time: parsedDate,
        [dataKey]: data[timestamp][dataKey],
      };
    })
    .filter((entry) => isAfter(entry.time, startDate));

  return (
    <ResponsiveContainer width="100%" height={300}>
      <LineChart data={chartData}>
        <CartesianGrid strokeDasharray="3 3" />

        {/* X-Axis with label */}
        <XAxis
          dataKey="time"
          tickFormatter={(time) => format(time, 'MMM d, HH:mm')}
          tick={{ fontSize: 12 }}
          label={{ value: 'Time', position: 'insideBottom', offset: -5 }}
        />

        {/* Y-Axis with dynamic label */}
        <YAxis
          label={{ 
            value: yAxisLabels[dataKey] || 'Sensor Value', 
            angle: -90, 
            position: 'insideLeft', 
            style: { textAnchor: 'middle' } 
          }}
        />

        <Tooltip labelFormatter={(time) => format(time, 'PPPpp')} />
        <Line type="monotone" dataKey={dataKey} stroke={color} strokeWidth={2} dot={false} />
      </LineChart>
    </ResponsiveContainer>
  );
};

export default SensorChart;
